.clearfix:after {
    clear: both;
}

.clearfix .helper {
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    width: 0;
}

a,
a:hover,
a:focus,
a:visited {
    text-decoration: none;
}

a, a:hover, a:focus,
input, input:hover, input:focus,
textarea, textarea:hover, textarea:focus,
button, button:hover, button:focus, select {
